export default {
    methods: {
        extract_gp_blocks(rawBlocks, tertiaryColour) {
            let blocks = this.split(rawBlocks)
            return this.formatBlocks(blocks, tertiaryColour)
        },
        split(raw) {
            // get content  between the <!-- wp:XXX --> markers
            // regex string finds a look behind and a look ahed for the wp gutenber comment stuctue
            //more info: https://regexr.com/2tr5t
            //https://stackoverflow.com/questions/6109882/regex-match-all-characters-between-two-strings

            //remove line breaks
            let noLBsRaw = raw.replace(/(\r\n|\n|\r)/gm, "")

            var pattern = /(?<=<!-- wp:[a-zA-Z-/]*({? [\s\S]*})? -->)(.*?)(?=<!-- \/wp:[a-zA-Z-/]* -->)/g

            return noLBsRaw.match(pattern)
        },
        formatBlocks(blocks, tertiaryColour) {
            //lopp through and format all the blocks
            //maybe a dictionary with the key being the needed component name

            blocks.forEach(function (blockString, index) {

                if(blockString.includes("wp-block-embed-youtube")) {
                    // get you tube link
                    // insert it into 
                    var patternYTurl = /(?<=">https:\/\/)(.*?)(?=<\/div>)/g
                    let url = blockString.match(patternYTurl)[0]

                    // youtube video coming from the CMS is not embedded
                    // need tho then embed the video

                    const regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
                    const match = url.match(regExp);
                
                    const videoId = (match && match[2].length === 11)
                      ? match[2]
                      : null;

                      blocks[index] = '<iframe  width="100%" height="315" src="//www.youtube.com/embed/' 
                          + videoId + '" frameborder="0" allowfullscreen></iframe>';
                }

                if(blockString.includes("wp-block-button")) {
                    var patternURL= /(?<=href=")(.*?)(?=">)/g
                    let url = blockString.match(patternURL)[0]

                    let textPattern = /(?<=href="[^]*">)(.*?)(?=<\/a><\/div>)/g
                    let text = blockString.match(textPattern)[0]

                    //if the block contans a # means it is one of the sites pages
                    // there don' open in a new tab
                    let target = blockString.includes("#") ? '' : 'target="_blank" '

                    blocks[index] = 
                    '<a class="flat-butt text-center" ' + 
                        'style="background-color: ' + tertiaryColour + 
                        '" ' +
                        target +
                        ' ' +
                        'href="' + url +
                        '" ><span style="padding-left: 10px;"> ' + 
                        text + 
                        '</span>' +
                    '</a>'

                    console.log(blocks[index])
                }
            });

            return blocks
        },
    }
}