<template>
  <div :id="isTA ? 'ta_view' : 'pw_view'" class="view intro hm-purple-light">
    <b-navbar toggleable="lg" type="dark" variant="info" class="bg-transparent">
      <b-navbar-brand href="#">
        <img id="nav-logo-img" src="@/assets/logo.jpg" alt="enginsoft-logo" />
        <span id="nav-logo-text">Tolerance Analysis</span>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item v-if="this.$store.getters.homePage">
            <router-link to="/">Home</router-link>
          </b-nav-item>
          <b-nav-item v-for="(page, index) in this.routes"
            :key="index">
            <router-link :to="page.post_title.toLowerCase().replace(/\s/g, '')">
              {{page.post_title}}
            </router-link>
          </b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <div class="container-fluid banner" id="top">
      <div class="container" id="text">
        <div class="col-md-5">
          <h1>
            {{siteTitle}}
          </h1>
          <hr />
          <p>{{siteDescription}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'underscore'

export default {
  name: "navbar",
  data () {
    return {
      siteTitle: process.env.VUE_APP_TITLE,
      siteDescription: process.env.VUE_APP_DESCRIPTION,
      isTA: process.env.VUE_APP_MODE == "TA"
    }
  },
  computed: {
    routes() {
      let r = _.sortBy(this.$store.state.pages, 'menu_order')
      return r.filter(x =>
        x.post_title.toLowerCase().replace(/\s/g, '')
        != "home")
    }
  }
};
</script>

<style scoped>
.view {
  height: 350px;
}

#ta_view {
    background: url("./bannerTA.jpg") no-repeat center center;
}

#pw_view {
    background: url("./bannerPW.png") no-repeat center center;
}

.banner {
  height: 60%;
  padding-bottom: 300px;
}

@media only screen and (max-width: 992px) {
  .banner#top {
    display: none;
  }
}

@media only screen and (min-width: 992px) {
  #nav-logo-text {
    display: none;
  }
}

#nav-logo-img {
  height: 40px;
  width: 55px;
  padding-right: 10px;
}

a {
  color: white;
}

#text {
  color: white;
  padding-top: 30px;
  padding-bottom: 50px;
}

p {
  font-size: 18px;
}

hr {
  border-color: white;
}
</style>
