<template> 
    <div class="content-styles">
        <div v-for="(block, index) in blocksFormatted"
        :key="index">
            <span class="content-styles" v-html="block"></span>
        </div>
    </div>
</template>

<script>
import extract_gp_blocks from "./gutenbergparser.js";

export default {
    mixins: [extract_gp_blocks],
    props: {
        blocksRaw: String
    },
    computed: {
        blocksFormatted() {
            return this.extract_gp_blocks(this.blocksRaw,  this.$store.state.tertiary_colour)
        }
    }
}
</script>

<style scoped>
.content-styles >>> hr {
  height: 2px;
  margin-bottom: 4%;
}

.content-styles >>> a {
    text-decoration: none;
    margin-bottom: 10px;
}
</style>