<template>
    <div>
        <three-bar-banner
        v-if="pageProp.acf.three_bar_banner"
        style="padding-bottom: 3%"/>
        <div class="container page-content" v-if="this.$store.state.pages">
          <div class="row">
          <page-content
          :contentProp="pageProp.post_content"
          :sidebarProp="pageProp.acf.sidebar"
          :slideToTextProp="pageProp.acf.slide_to_button_text"></page-content>
          </div>
          <div v-if="productList.length > 0">
            <div v-for="(productName, index) in productList"
              :key="index">
                <product-block
                :productName="productName"/>
            </div>
          </div>
      </div>
      <div v-if="pageProp.acf.map_present">
          <Maps id="map-area"></Maps>
      </div>
    </div>
</template>

<script>
import PageContent from "@/views/PageContent/PageContent.vue"
import ProductBlock from "@/views/PageContent/ProductBlock.vue"
import ThreeBarBanner from "@/components/ThreeBarBanner.vue"
import Maps from "@/components/Map.vue"


export default {
  components: {
    ProductBlock,
    PageContent,
    ThreeBarBanner,
    Maps
  },
  props: {
      pageProp: Object
  },
  computed: {
    productList() {
        return this.pageProp.acf.products.split(' ')
    }
  }
};
</script>

<style scoped>
#map-area {
  padding-top: 5%;
}
</style>
